___CSS_LOADER_EXPORT___.push([module.id, "* {\n    padding: 0;\n    margin: 0;\n}\n\nbody {\n    font-family: 'Poppins', sans-serif;\n    background-color: white;\n    min-height: 100vh;\n    display: flex;\n    flex-direction: column;\n}\n\na {\n    padding: 14px 0 !important;\n}\n\nbutton {\n    padding: 15px 15px !important;\n}\n\n/*\n    Skip Link\n*/\n\n.skip-link {\n    position: absolute;\n    left: 0;\n    background-color: #15ad66;\n    color: white;\n    padding: 8px;\n    z-index: 100;\n}\n\n.skip-link-top {\n    top: -55px;\n}\n\n.skip-link-top:focus {\n    top: 0;\n}\n\n.upArrow {\n    width: 55px;\n    height: 55px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: #15ad66;\n\n    position: fixed;\n    bottom: 16px;\n    right: 16px;\n    border-radius: 50%;\n    border: 0;\n\n    font-size: 18px;\n    color: white;\n    cursor: pointer;\n\n    display: none;\n}\n/*\n    AppBar\n*/\n\n.app-bar {\n    padding: 8px 16px;\n    background-color: white;\n    display: grid;\n    grid-template-columns: auto 1fr auto;\n    gap: 10px;\n    position: sticky;\n    top: 0;\n    z-index: 99;\n    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);\n}\n\n.app-bar .app-bar__menu {\n    display: flex;\n    align-items: center;\n}\n\n.app-bar .app-bar__menu button {\n    background-color: transparent;\n    border: none;\n    font-size: 18px;\n    cursor: pointer;\n    /* margin-left: 20px; */\n}\n\n.app-bar .app-bar__brand {\n    display: flex;\n    align-items: center;\n}\n\n.app-bar .app-bar__brand h1 {\n    /* color: #4F200D; */\n    color: #15ad66;\n    font-family: 'Righteous', cursive;\n    font-size: 22px;\n    user-select: none;\n}\n\n.app-bar .app-bar__navigation {\n    position: absolute;\n    top: 50px;\n    left: -180px;\n    width: 150px;\n    transition: all 0.3s;\n    padding: 8px;\n    background-color: white;\n    overflow: hidden;\n    font-family: 'Righteous', cursive;\n}\n\n.app-bar .app-bar__navigation.open {\n    left: 0;\n}\n\n.app-bar .app-bar__navigation ul li a {\n    display: inline-block;\n    text-decoration: none;\n    color: black;\n    padding: 8px;\n    margin-bottom: 5px;\n    width: 100%;\n}\n\n.app-bar__navigation ul li a:hover {\n    color: #15ad66;\n}\n\n/*\nHero \n*/\n\n/* hero-comp {\n    display: block;\n    align-items: center;\n    min-height: 320px;\n    width: 100%;\n    text-align: center;\n    background-image: url(\"../public/images/heros/hero-image_2.jpg\");\n    background-position: center;\n    background-repeat: no-repeat;\n    background-size: cover;\n} */\n\nhero-comp{\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 100%;\n    height: 320px;\n    overflow: hidden;\n    position: relative;\n}\n\n#hero{\n    width: 100vw;\n    height: auto;\n    /* position: absolute; */\n}\n\n/*\n    Main Content\n*/\n\nmain {\n    padding: 32px;\n    flex: 1;\n}\n\n.content {\n    margin: 0 auto;\n    min-height: 100%;\n}\n\n.content .content__heading {\n    font-family: 'Righteous', cursive;\n    font-weight: normal;\n    margin-top: 20px;\n}\n\n\n/*\n    restaurants\n*/\n\n.restaurants {\n    display: grid;\n    grid-template-columns: 1fr;\n    gap: 16px;\n    margin: 32px 0;\n}\n\n.icon {\n    width: 32px;\n}\n\n\n/*\n    restaurant Item\n*/\n\n.restaurant-item {\n    width: 100%;\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n    border-radius: 8px;\n    overflow: hidden;\n}\n\n.restaurant-item__header {\n    position: relative;\n}\n\n.restaurant-item .restaurant-item__header .restaurant-item__header__poster {\n    width: 100%;\n    height: 250px;\n}\n\n.restaurant-item .restaurant-item__header .restaurant-item__header__rating {\n    position: absolute;\n    padding: 8px;\n    bottom: 20px;\n    right: 0;\n    display: inline-block;\n    background-color: white;\n    color: black;\n    font-weight: bold;\n    border-radius: 25px 0 0 25px;\n}\n\n.restaurant-item .restaurant-item__header .restaurant-item__header__rating .restaurant-item__header__rating__score {\n    margin-left: 10px;\n}\n\n.restaurant-item .restaurant-item__content {\n    padding: 16px;\n}\n\n.restaurant-item .restaurant-item__content h3 {\n    margin: 0 0 10px 0;\n}\n\n.restaurant-item .restaurant-item__content h3 a {\n    color: #15ad66;\n    text-decoration: none;\n}\n\n.restaurant-item .restaurant-item__content p {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    display: -webkit-box;\n    -webkit-line-clamp: 4;\n    /* number of lines to show */\n    -webkit-box-orient: vertical;\n}\n\n.star-rate{\n    width: 20px;\n    height: auto;\n    vertical-align: top;\n}\n\n/*\n    restaurant detail\n*/\n\n.restaurant {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    font-family: Arial, sans-serif;\n}\n\n.restaurant .restaurant__poster {\n    width: 100%;\n    max-width: 300px;\n    object-fit: cover;\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n    transition: 0.3s;\n}\n\n.restaurant .restaurant__poster:hover {\n    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);\n}\n\n.restaurant__subTitle {\n    /* font-family: 'Righteous', cursive; */\n    font-family: 'Geologica', sans-serif;\n    font-weight: 500;\n    color: black;\n    font-size: 18px;\n}\n\n.restaurant__subTitleBold {\n    font-family: 'Geologica', sans-serif;\n    font-weight: 800;\n    color: white;\n    background-color: black;\n    padding: 10px;\n    /* font-size: 24px; */\n    margin: 10px;\n}\n\n.restaurant__subTitleBold p {\n    font-size: 26px !important;\n}\n\n.restaurant__details {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    margin-top: 20px;\n    padding: 20px;\n    /* background-color: #f2f2f2; */\n    border-radius: 10px;\n    /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); */\n    /* width: 600px; */\n    max-width: 100%;\n}\n\n.restaurant__name {\n    font-family: 'Righteous', cursive;\n    font-size: 24px;\n    font-weight: bold;\n    margin-bottom: 10px;\n}\n\n.restaurant__address {\n    font-size: 16px;\n    margin-bottom: 10px;\n}\n\n.restaurant__rating {\n    font-size: 16px;\n    margin-bottom: 10px;\n    font-weight: bold;\n}\n\n.tag__categories {\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n    margin-bottom: 10px;\n}\n\n.tag {\n    background-color: #f2f2f2;\n    border: 1px solid #ccc;\n    border-radius: 20px;\n    padding: 5px 10px;\n    margin: 5px;\n}\n\n.restaurant__description{\n    text-align: center;\n    margin-bottom: 10px;\n}\n\n\n.restaurant__menu , .restaurant__food, .restaurant__drink{\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n    margin-bottom: 20px;\n}\n\n.food__item, .drink__item  {\n    background-color: #f2f2f2;\n    border: 1px solid #ccc;\n    border-radius: 10px;\n    padding: 10px;\n    margin: 10px;\n    width: 200px;\n    text-align: center;\n}\n\n.food__item:hover{\n    background-color: #15ad66;\n    color: white;\n}\n\n.drink__item:hover{\n    background-color: #12dd7e;\n    color: white;\n}\n\n\n.restaurant__details h1 {\n    font-size: 28px;\n    font-weight: bold;\n    margin-bottom: 10px;\n}\n\n.restaurant__details h2 {\n    font-size: 20px;\n    font-weight: bold;\n    margin-bottom: 10px;\n}\n\n.restaurant__details p {\n    font-size: 16px;\n}\n/*\n    Like\n*/\n.like {\n    width: 55px;\n    height: 55px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: #db0000;\n\n    position: fixed;\n    bottom: 87px;\n    right: 16px;\n    border-radius: 50%;\n    border: 0;\n\n    font-size: 18px;\n    color: white;\n    cursor: pointer;\n}\n\n\n/*\n    Review Section\n*/\n\n#restaurant__review__container{\n    align-items: center;\n    justify-content: center;\n    display: flex;\n    flex-direction: column;\n}\n\nreview-list, #restaurant__Newreview {\n    /* align-self: stretch; */\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n}\n\n.restaurant__reviews{\n    align-self: stretch;\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n}\n\nreview-item, .restaurant__Newcomment {\n    margin: 10px;\n    background-color: #f9f9f9;\n    padding: 15px;\n    border-radius: 10px;\n    width: 80vw;\n    overflow-wrap: break-word;\n    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);\n}\n\n.restaurant__Newcomment .restaurant__author, .restaurant__author {\n    font-weight: bold;\n}\n\n.restaurant__Newcomment .restaurant__timestamp, .restaurant__timestamp {\n    font-size: 12px;\n    color: #888;\n}\n\n.restaurant__Newcomment .restaurant__message, .restaurant__message {\n    margin-top: 15px;\n}\n\n/*\n    Restaurant Add Review\n*/\n\n.restaurant__addReview{\n    margin: 10px;\n    margin-bottom: 30px;\n    background-color: #f9f9f9;\n    padding: 15px;\n    border-radius: 10px;\n    width: 60vw;\n    overflow-wrap: break-word;\n    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);\n}\n\n.restaurant__addReview label {\n    display: block;\n    margin-bottom: 5px;\n    font-weight: 500;\n    font-family: 'Geologica', sans-serif;\n}\n\n#addReviewName, #addReviewText {\n    font-family: Arial, sans-serif;\n    width: 58vw;\n    max-width: 58vw;\n    padding: 15px 5px;\n    border: 1px solid #ccc;\n    border-radius: 5px;\n    margin: 10px auto;\n}\n\n#addReviewName:focus,\n#addReviewText:focus {\n    outline-color: #15ad66;\n}\n\n.restaurant__addReview button{\n    padding: 15px 10px;\n    background-color: #4caf50;\n    color: #fff;\n    border: none;\n    border-radius: 3px;\n    cursor: pointer;\n    /* width: 100%; */\n    margin: auto;\n}\n\n.restaurant__addReview button:hover {\n    background-color: #45a049;\n}\n\n.errorMessage {\n    color: red;\n    font-size: 12px;\n    display: block;\n    margin-bottom: 10px;\n}\n\n/*\n    Favorite\n*/\n\n.restaurant__emptyMessage{\n    text-align: center;\n    font-size: small;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n\n/*\n    Footer\n*/\n\nfooter {\n    padding: 16px;\n}\n\nfooter p {\n    text-align: center;\n    color: #aaaaaa;\n}\n\nfooter p a {\n    color: black;\n    text-decoration: none;\n}\n\n/*\n    Loader\n*/\n\n.loaderContainer {\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background-color: rgba(255, 255, 255, 0.5);\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    z-index: 9999;\n    display: flex;\n    flex-direction: column;\n    text-align: center;\n}\n\n\n.loader {\n    display: inline-block;\n    width: 80px;\n    height: 80px;\n}\n\n.loader:after {\n    content: \" \";\n    display: block;\n    width: 64px;\n    height: 64px;\n    margin: 8px;\n    border-radius: 50%;\n    border: 6px solid #45a049;\n    border-color: #45a049 transparent #45a049 transparent;\n    animation: loader 1s linear infinite;\n}\n\n/*\n    404 Not Found\n*/\n\n.Page404{\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    margin: auto 20px;\n}\n\n.Page404 h1 {\n    font-size: 48px;\n    color: #333333;\n    margin-bottom: 20px;\n}\n\n.Page404 p {\n    font-size: 24px;\n    color: #666666;\n    margin-bottom: 40px;\n}\n\n@keyframes loader {\n    0% {\n        transform: rotate(0deg);\n    }\n\n    100% {\n        transform: rotate(360deg);\n    }\n}", "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;IACI,UAAU;IACV,SAAS;AACb;;AAEA;IACI,kCAAkC;IAClC,uBAAuB;IACvB,iBAAiB;IACjB,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,0BAA0B;AAC9B;;AAEA;IACI,6BAA6B;AACjC;;AAEA;;CAEC;;AAED;IACI,kBAAkB;IAClB,OAAO;IACP,yBAAyB;IACzB,YAAY;IACZ,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,MAAM;AACV;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,yBAAyB;;IAEzB,eAAe;IACf,YAAY;IACZ,WAAW;IACX,kBAAkB;IAClB,SAAS;;IAET,eAAe;IACf,YAAY;IACZ,eAAe;;IAEf,aAAa;AACjB;AACA;;CAEC;;AAED;IACI,iBAAiB;IACjB,uBAAuB;IACvB,aAAa;IACb,oCAAoC;IACpC,SAAS;IACT,gBAAgB;I